@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Roboto+Condensed:wght@300&display=swap');


.outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background: #c31432;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  
  
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: 49, 192, 243, 1;
  border-radius: 20px;
  height: 85%;
  width: 45%;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 20px;
  transition:0.3s ease-out;
  
}

.container:hover {
  transform:scale(1.01) translateY(-26px);
  -webkit-box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
  -moz-box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
  box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
  background: linear-gradient(-45deg, #8728ac, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}

@keyframes gradient {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}

.ziggy{
  text-align: center;
  background: #c31432;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  color:white;
  height: 100vh;
  margin-top: -50px;
  padding-top: 100px;
  
  
  
}

.ziggy h1 {
  font-family: 'Black Ops One', cursive;
  font-size: 40px;
  letter-spacing: 3px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .outerContainer {
    height: 100%;
  }

  .container {
    width: 100%;
    
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  .container {
    width: 60%;
  }
}
