html, body {
  font-family: 'Roboto', sans-serif;
  padding: 0;
  margin: 0;
}

#root {
  height: 100vh;
  
}

* {
  box-sizing: border-box;
}

.joinOuterContainer {
  display: flex;
  justify-content: center;
  text-align: center;
  height: 100vh;
  align-items: center;
  background: #c31432;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.joinInnerContainer {
  width: 20%;
}

.joinInput {
  border-radius: 10px;
  padding: 15px 20px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.599);
  color: #fff;
}

.heading {
  color: white;
  font-size: 2.5em;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(0, 0, 0);
  letter-spacing: 4px;
}

.button {
    color: #fff !important;
    font-size: 21px;
    text-transform: uppercase;
    text-decoration: none;
    background: #0e0054 !important;
    padding: 20px;
    border-radius: 15px;
    display: inline-block;
    border: none;
    width: 100%;
    transition: 0.3s ease-out;
}

.button:hover {
    background:navy !important;
    color: red !important;
    letter-spacing:6px;
    font-size:30px;
    border-radius:5px;
    width:90%;
   
    
}

.button:active {
  
  transform:scale(1.15) translateY(16px);
  -webkit-box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
  -moz-box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
  box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
  
}

.mt-20 {
  margin-top: 20px;
  background-color: rgba(255, 255, 255, 0.599);
  color: rgb(255, 255, 255);
}

@media (min-width: 320px) and (max-width: 480px) {
  .joinOuterContainer {
    height: 100%;
  }

  .joinInnerContainer {
    width: 90%;
  }


}

button:focus {
  outline: 0;
}