.form {
  display: flex;
  border-top: 0px solid #8c8c8c;
}

.input {
  border: none;
  border-radius: 20px;
  padding: 0 2%;
  width: 80%;
  font-size: 1em;
}

input:focus, textarea:focus, select:focus{
  outline: none;
}

.sendButton {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 4px;
  background: #2979FF;
  padding: 20px;
  margin: 0 5px;
  display: inline-block;
  border: none;
  width: 20%;
  border-radius: 20px;
  
}