@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Roboto+Condensed:wght@300&display=swap');

.textContainer {
  display: flex;
  flex-direction: column;
  margin-left: 100px;
  color: white;
  height: 50%;
  justify-content: space-between;
  
  background: rgba(36, 23, 23, 0);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(14.3px);
  -webkit-backdrop-filter: blur(14.3px);
  
  padding: 15px;
  border-radius: 20px;
  font-family: 'Roboto Condensed', sans-serif;
  
  transition: 0.3s ease-out;

}
.textContainer:hover {
  rotate: 5deg;
  background: rgba(36, 23, 23, 0.9);
  transform:scale(1.15) translateY(16px);
  -webkit-box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
  -moz-box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
  box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
}
.textContainer h4 {
  font-family: 'Black Ops One', cursive;
  font-size: 30px;
  color: rgb(220, 171, 107);
}

.activeContainer {
  display: flex;
  align-items: center;
  margin-bottom: 50%;
}

.activeItem {
  display: flex;
  align-items: center;
}

.activeContainer img {
  padding-left: 10px;
}

.textContainer h1 {
  margin-bottom: 0px;
}

@media (min-width: 320px) and (max-width: 1200px) {
  .textContainer {
    display: none;
  }
}