@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Roboto+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Roboto+Condensed:wght@300&display=swap);
.textContainer {
  display: flex;
  flex-direction: column;
  margin-left: 100px;
  color: white;
  height: 50%;
  justify-content: space-between;
  
  background: rgba(36, 23, 23, 0);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(14.3px);
  -webkit-backdrop-filter: blur(14.3px);
  
  padding: 15px;
  border-radius: 20px;
  font-family: 'Roboto Condensed', sans-serif;
  
  -webkit-transition: 0.3s ease-out;
  
  transition: 0.3s ease-out;

}
.textContainer:hover {
  rotate: 5deg;
  background: rgba(36, 23, 23, 0.9);
  -webkit-transform:scale(1.15) translateY(16px);
          transform:scale(1.15) translateY(16px);
  box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
}
.textContainer h4 {
  font-family: 'Black Ops One', cursive;
  font-size: 30px;
  color: rgb(220, 171, 107);
}

.activeContainer {
  display: flex;
  align-items: center;
  margin-bottom: 50%;
}

.activeItem {
  display: flex;
  align-items: center;
}

.activeContainer img {
  padding-left: 10px;
}

.textContainer h1 {
  margin-bottom: 0px;
}

@media (min-width: 320px) and (max-width: 1200px) {
  .textContainer {
    display: none;
  }
}
.messageBox {
  background: #F3F3F3;
  border-radius: 10px;
  padding: 0 10px ;
  color: white;
  display: inline-block;
  max-width: 80%;
  
}

.messageText {
  width: 100%;
  letter-spacing: 0;
  float: left;
  font-size: 1.1em;
  word-wrap: break-word;
}

.messageText img {
  vertical-align: middle;
}

.messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0 5%;
  margin-top: 3px;
}

.sentText {
  display: flex;
  align-items: center;
  font-family: Helvetica;
  color: #828282;
  letter-spacing: 0.3px;
}

.pl-10 {
  padding-left: 10px;
}

.pr-10 {
  padding-right: 10px;
}

.justifyStart {
  justify-content: flex-start;
}

.justifyEnd {
  justify-content: flex-end;
}

.colorWhite {
  color: white;
}

.colorDark {
  color: #353535;
}

.backgroundBlue {
  background: #2979FF;
}

.backgroundLight {
  background: #F3F3F3;
}

.messages {
  padding: 0;
  overflow: auto;
  flex: auto;
}
.infoBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #2978ff54;
  border-radius: 15px 15px 0 0;
  height: 60px;
  width: 100%;
}

.leftInnerContainer {
  flex: 0.5 1;
  display: flex;
  align-items: center;
  margin-left: 5%;
  color: white;
}

.rightInnerContainer {
  display: flex;
  flex: 0.5 1;
  justify-content: flex-end;
  margin-right: 5%;
}

.onlineIcon {
  margin-right: 5%;
}

.form {
  display: flex;
  border-top: 0px solid #8c8c8c;
}

.input {
  border: none;
  border-radius: 20px;
  padding: 0 2%;
  width: 80%;
  font-size: 1em;
}

input:focus, textarea:focus, select:focus{
  outline: none;
}

.sendButton {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 4px;
  background: #2979FF;
  padding: 20px;
  margin: 0 5px;
  display: inline-block;
  border: none;
  width: 20%;
  border-radius: 20px;
  
}
.outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background: #c31432;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-gradient(linear, right top, left top, from(#240b36), to(#c31432));
background: -webkit-linear-gradient(right, #240b36, #c31432);
background: linear-gradient(to left, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  
  
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: 49, 192, 243, 1;
  border-radius: 20px;
  height: 85%;
  width: 45%;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 20px;
  -webkit-transition:0.3s ease-out;
  transition:0.3s ease-out;
  
}

.container:hover {
  -webkit-transform:scale(1.01) translateY(-26px);
          transform:scale(1.01) translateY(-26px);
  box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
  background: -webkit-linear-gradient(135deg, #8728ac, #e73c7e, #23a6d5, #23d5ab);
  background: linear-gradient(-45deg, #8728ac, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  -webkit-animation: gradient 15s ease infinite;
          animation: gradient 15s ease infinite;
}

@-webkit-keyframes gradient {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}

@keyframes gradient {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}

.ziggy{
  text-align: center;
  background: #c31432;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-gradient(linear, right top, left top, from(#240b36), to(#c31432));
background: -webkit-linear-gradient(right, #240b36, #c31432);
background: linear-gradient(to left, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  color:white;
  height: 100vh;
  margin-top: -50px;
  padding-top: 100px;
  
  
  
}

.ziggy h1 {
  font-family: 'Black Ops One', cursive;
  font-size: 40px;
  letter-spacing: 3px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .outerContainer {
    height: 100%;
  }

  .container {
    width: 100%;
    
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  .container {
    width: 60%;
  }
}

html, body {
  font-family: 'Roboto', sans-serif;
  padding: 0;
  margin: 0;
}

#root {
  height: 100vh;
  
}

* {
  box-sizing: border-box;
}

.joinOuterContainer {
  display: flex;
  justify-content: center;
  text-align: center;
  height: 100vh;
  align-items: center;
  background: #c31432;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #240b36, #c31432);  /* Chrome 10-25, Safari 5.1-6 */
background: -webkit-gradient(linear, right top, left top, from(#240b36), to(#c31432));
background: -webkit-linear-gradient(right, #240b36, #c31432);
background: linear-gradient(to left, #240b36, #c31432); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.joinInnerContainer {
  width: 20%;
}

.joinInput {
  border-radius: 10px;
  padding: 15px 20px;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.599);
  color: #fff;
}

.heading {
  color: white;
  font-size: 2.5em;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(0, 0, 0);
  letter-spacing: 4px;
}

.button {
    color: #fff !important;
    font-size: 21px;
    text-transform: uppercase;
    text-decoration: none;
    background: #0e0054 !important;
    padding: 20px;
    border-radius: 15px;
    display: inline-block;
    border: none;
    width: 100%;
    -webkit-transition: 0.3s ease-out;
    transition: 0.3s ease-out;
}

.button:hover {
    background:navy !important;
    color: red !important;
    letter-spacing:6px;
    font-size:30px;
    border-radius:5px;
    width:90%;
   
    
}

.button:active {
  
  -webkit-transform:scale(1.15) translateY(16px);
  
          transform:scale(1.15) translateY(16px);
  box-shadow: 0px 0px 36px -3px rgba(255,46,99,1);
  
}

.mt-20 {
  margin-top: 20px;
  background-color: rgba(255, 255, 255, 0.599);
  color: rgb(255, 255, 255);
}

@media (min-width: 320px) and (max-width: 480px) {
  .joinOuterContainer {
    height: 100%;
  }

  .joinInnerContainer {
    width: 90%;
  }


}

button:focus {
  outline: 0;
}
